
import { defineComponent } from "vue";

export default defineComponent({
  data() {
    return {
      text: "",
      showText: false,
    };
  },
  methods: {
    displayLocation() {
      switch (this.$route.path.split("/")[2]) {
        case "customers":
          this.text = "klienta";
          break;
        case "companies":
          this.text = "firme";
          break;
        case "commodities":
          this.text = "towar";
          break;
        case "suppliers":
          this.text = "dostawce";
          break;
        case "orders":
          this.text = "zamówienie";
          break;
        default:
          this.text = "";
          break;
      }
    },
  },
  mounted() {
    this.displayLocation();
  },
});
