
import InfoBlackIcon from "@/icons/InfoBlackIcon.vue";
import { defineComponent } from "vue";

export default defineComponent({
  components: { InfoBlackIcon },
  props: {
    name: {
      type: String,
      default: "Element name",
    },
    quantity: {
      type: Number,
      default: 0,
    },
    id: {
      type: String,
      required: true,
    },
    type: {
      type: String,
      required: true,
    },
  },
});
